import Events from '../Constant/EventType';
import GetLocaleEvent from '../Event/GetLocaleEvent';
import GetInfoSystemEvent from '../Event/GetInfoSystemEvent';
import GetCredentialsEvent from '../Event/GetCredentialsEvent';
import SaveCredentialsEvent from '../Event/SaveCredentialsEvent';
import GetMappedChannelsEvent from '../Event/GetMappedChannelsEvent';
import GetSalesChannelsEvent from '../Event/GetSalesChannelsEvent';
import SaveMappedChannelEvent from '../Event/SaveMappedChannelEvent';
import GetTrustbadgeConfigurationEvent from '../Event/GetTrustbadgeConfigurationEvent';
import GetProductReviewForChannelEvent from '../Event/GetProductReviewForChannelEvent';
import SaveTrustbadgeConfigurationEvent from '../Event/SaveTrustbadgeConfigurationEvent';
import GetWidgetEvent from '../Event/GetWidgetEvent';
import SaveWidgetEvent from '../Event/SaveWidgetEvent';
import ActivateProductReviewEvent from '../Event/ActivateProductReviewEvent';
import DeactivateProductReviewEvent from '../Event/DeactivateProductReviewEvent';
import DisconnectEvent from '../Event/DisconnectEvent';
import ExportOrdersEvent from "../Event/ExportOrdersEvent";
import GetProductIdentifiersEvent from "../Event/GetProductIdentifiersEvent";
import GetWidgetLocationEvent from "../Event/GetWidgetLocationEvent";
import GetOrderStatusEventsEvent from "../Event/GetOrderStatusEventsEvent";
import SaveOrderStatusEventsEvent from "../Event/SaveOrderStatusEventsEvent";

export default class EventFactory {

  getEvent(eventName) {
    switch (eventName) {
      case Events.GET_LOCALE.name:
        return new GetLocaleEvent();
      case Events.GET_INFORMATION_OF_SYSTEM.name:
        return new GetInfoSystemEvent();
      case Events.GET_CREDENTIALS_PROVIDED.name:
        return new GetCredentialsEvent();
      case Events.SAVE_CREDENTIALS.name:
        return new SaveCredentialsEvent();
      case Events.GET_MAPPED_CHANNELS.name:
        return new GetMappedChannelsEvent();
      case Events.GET_SALES_CHANNELS_PROVIDED.name:
        return new GetSalesChannelsEvent();
      case Events.SAVE_MAPPED_CHANNEL.name:
        return new SaveMappedChannelEvent();
      case Events.GET_TRUSTBADGE_CONFIGURATION_PROVIDED.name:
        return new GetTrustbadgeConfigurationEvent();
      case Events.SAVE_TRUSTBADGE_CONFIGURATION.name:
        return new SaveTrustbadgeConfigurationEvent();
      case Events.GET_PRODUCT_REVIEW_FOR_CHANNEL.name:
        return new GetProductReviewForChannelEvent();
      case Events.GET_WIDGET_PROVIDED.name:
        return new GetWidgetEvent();
      case Events.SAVE_WIDGET_CHANGES.name:
        return new SaveWidgetEvent();
      case Events.ACTIVATE_PRODUCT_REVIEW_FOR_CHANNEL.name:
        return new ActivateProductReviewEvent();
      case Events.DEACTIVATE_PRODUCT_REVIEW_FOR_CHANNEL.name:
        return new DeactivateProductReviewEvent();
      case Events.DISCONNECTED.name:
        return new DisconnectEvent();
      case Events.EXPORT_PREVIOUS_ORDER.name:
        return new ExportOrdersEvent();
      case Events.GET_AVAILABLE_PRODUCT_IDENTIFIERS.name:
        return new GetProductIdentifiersEvent();
      case Events.GET_LOCATION_FOR_WIDGET.name:
        return new GetWidgetLocationEvent();
      case Events.GET_USE_EVENTS_BY_ORDER_STATUS_FOR_CHANNEL.name:
        return new GetOrderStatusEventsEvent();
      case Events.SAVE_USE_EVENTS_BY_ORDER_STATUS_FOR_CHANNEL.name:
        return new SaveOrderStatusEventsEvent();
      default:
        throw new Error(`Cannot get the event ${eventName}`);
    }
  }
};